@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700,800&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700,800&display=swap);
.product_cart_form {
  display: flex;
  justify-content: flex-start;
  width: 100%;
  margin-bottom: 1rem;
}

.product_cart_form form {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 130px;
}

.product_cart_form label {
  margin: 0;
  font-weight: 400;
}

.product_cart_form input {
  width: 50px;
  height: 30px;
  border: 1px solid #999999;
  text-align: center;
}

.product_cart_form input[type="number"]::-webkit-inner-spin-button,
.product_cart_form input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.product_cart_form button {
  flex-basis: 25%;
  height: 30px;
  margin-left: 50px;
  background-color: #3195c9;
  color: #fff;
  border: none;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

.product_cart_form button:hover {
  background-color: #2386c0;
}

.products {
  position: relative;
  width: 100vw;
  display: flex;
  flex-wrap: wrap;
}

.product {
  flex-basis: 60%;
  border-bottom: 1px solid #ddd;
  margin: 0 auto;
  min-height: 30vh;
}

.product:hover {
  background-color: #ddd;
}

.product_container {
  display: flex;
  height: 100%;
  justify-content: space-around;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

.product_container:hover {
  background-color: #ddd;
}

.product_container_image {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex-basis: 20%;
  padding: 0;
  margin: 20px 0;
  border: 1px solid #ddd;
  border-radius: 4px;
  background-color: #fff;
}

.product_container_image:hover {
  cursor: pointer;
}

.product_container_content {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: flex-start;
  flex-basis: 60%;
}

.product_container_content p:nth-child(2) {
  margin-bottom: 4rem;
  font-size: 16px;
}

.btn_show_modal {
  border: none;
  background-color: inherit;
  padding: 10px 20px 10px 0;
  font-size: 24px;
  text-align: left;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

.btn_show_modal:hover {
  color: #2386c0;
}

.btn_show_modal p {
  margin: 0;
}

.overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
}

.overlay.show {
  display: block;
  z-index: 4;
}

.products .modal {
  display: none;
  background: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 80vh;
  height: 90vh;
}

.products .modal.show {
  display: block;
}

.modal_content {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  padding: 30px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: flex-start;
}

.modal_content .icon-cancel {
  display: inline-block;
  position: absolute;
  right: 0;
  top: 0;
  font-size: 30px;
}

.modal_content .image {
  width: 65%;
  margin: 0 auto;
}

.modal_content .img {
  display: block;
  width: 100%;
  margin: 0 auto;
}

.modal_content p {
  margin: 0;
}

.modal_content p:nth-child(3) {
  font-size: 24px;
}

.modal_content p:nth-child(4) {
  font-size: 20px;
}

.modal_content p:nth-child(5) {
  font-size: 12px;
  margin: 0;
  text-align: justify;
}

.pagin {
  display: flex;
  justify-content: center;
  margin: 50px 0;
}

.icon-left-open-big,
.icon-right-open-big {
  margin: 0 40px;
  font-size: 40px;
  color: #999999;
  cursor: pointer;
}

.invisible {
  opacity: 0;
}

.pagin form {
  width: 150px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.pagin input {
  width: 50px;
  height: 40px;
  border: 1px solid #999999;
  text-align: center;
}

.pagin input[type="number"]::-webkit-inner-spin-button,
.pagin input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.form {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.form label {
  margin-right: 10px;
  font-weight: 400;
}

.form input {
  width: 300px;
  line-height: 40px;
  padding: 0;
}

.form input::-webkit-input-placeholder {
  padding-left: 10px;
}

.form input::-moz-placeholder {
  padding-left: 10px;
}

.form input:-ms-input-placeholder {
  padding-left: 10px;
}

.form input::-ms-input-placeholder {
  padding-left: 10px;
}

.form input::placeholder {
  padding-left: 10px;
}

.form button {
  width: 120px;
  line-height: 42px;
  background-color: #3195c9;
  color: #fff;
  border: none;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

.form button:hover {
  background-color: #2386c0;
}

.form form button:nth-child(4) {
  background-color: inherit;
  color: #000;
  border: 1px solid #a9a9a9;
  line-height: 40px;
}

/* .cart .modal_content  */
.cart_item {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 90%;
  border-bottom: 1px solid #ddd;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

.cart_item:hover {
  background-color: #ddd;
}

.cart_item_img {
  margin: 15px 0;
}

.cart img {
  margin: 0 auto;
  display: block;
  max-height: 100px;
  max-width: 100px;
  width: 100%;
  padding: 4px;
  border: 1px solid #ddd;
  border-radius: 4px;
  background-color: white;
}

.cart_item_product {
  flex-basis: 50%;
}

.icon-cancel,
.icon-cancel-circled-outline {
  cursor: pointer;
}

.icon-cancel-circled-outline {
  color: red;
}

.cart_icon {
  position: absolute;
  top: 15px;
  right: 60px;
  width: 50px;
  height: 50px;
  cursor: pointer;
}

.cart_number {
  position: absolute;
  top: 20px;
  right: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  background-color: #ff0033;
  color: #fff;
  font-weight: 600;
  border-radius: 15px;
}

.cart_icon .icon-basket {
  position: absolute;
  top: -16px;
  right: -13px;
  z-index: -1;
  font-size: 55px;
  color: #0277bd;
}

.cart .overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
}

.cart .overlay.show {
  display: block;
  z-index: 4;
}

.cart .modal {
  display: none;
  background: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 70vw;
  height: 95%;
}

.cart .modal.show {
  display: block;
}

.cart .modal_content {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  padding: 30px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  overflow: auto;
}

.cart .modal_content_empty {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  padding: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.cart .modal_content .icon-cancel {
  display: inline-block;
  position: absolute;
  right: 0;
  top: 0;
  font-size: 30px;
}

.cart_remove_all {
  display: flex;
  align-self: flex-end;
  margin: 20px 77px 0 0;
}

.modal_content .cart_remove_all p {
  margin-right: 20px;
}

.icon-trash-empty {
  cursor: pointer;
}

.icon-basket_empty {
  font-size: 60px;
}

body {
  font-family: "Open Sans", sans-serif;
}

img {
  display: block;
  max-height: 150px;
  max-width: 210px;
}

